@font-face {
  font-family: "Raleway-Regular";
  src: url("../fonts/Raleway-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway-Medium";
  src: url("../fonts/Raleway-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Raleway-Regular", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #353A5A;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.title {
  margin: 0;
  font-family: "Raleway-Medium";
  font-weight: 500;
  font-size: 58px;
  line-height: 100%;
}

.text {
  margin: 0;
}

.menu {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu__item {
  margin-right: 40px;
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__item:hover .menu__link::after {
  width: 100%;
}
.menu__link {
  position: relative;
  color: #FDFDFD;
}
.menu__link::after {
  content: "";
  position: absolute;
  width: 0;
  border-top: 2px dashed #FA9F42;
  left: 50%;
  top: 120%;
  transform: translateX(-50%);
  transition: width 0.4s;
}

.logo {
  margin-bottom: 40px;
  max-width: 170px;
  line-height: 0;
}
.logo__img {
  width: 100%;
  height: auto;
}

.link {
  padding: 20px 45px;
  background-color: #FA9F42;
  color: #FDFDFD;
  border-radius: 0px 20px;
  transition: border-radius 0.4s;
}
.link:hover {
  border-radius: 20px 0;
}

.net {
  line-height: 0;
}

.icon {
  width: 22px;
  height: 22px;
}

.characteristic {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.characteristic--indent {
  margin-bottom: 120px;
}
.characteristic__inner {
  max-width: 225px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.characteristic__heading {
  margin-bottom: 15px;
  font-family: "Raleway-Medium";
  font-weight: 500;
  font-size: 58px;
  line-height: 100%;
  color: #10B582;
}
.characteristic__desc {
  font-size: 14px;
  line-height: 120%;
  text-align: center;
}

